import { css } from '@emotion/css';
import { IBase64ImagePlaceholder, IImagePath } from 'common';
import { Colors, mobileMediaQuery } from 'components';

export const choosePageStyle = css({
  width: '100%',
  height: window.innerHeight,
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  flexDirection: 'column'
});

export const choosePageImageStyle = (
  background: IImagePath | IBase64ImagePlaceholder,
  isPixelated: boolean
): string => css({
  position: 'fixed',
  top: 0,
  left: 0,
  width: '100%',
  height: '100%',
  backgroundImage: `url('${background as unknown as string}')`,
  backgroundPosition: 'center center',
  backgroundRepeat: 'repeat',
  backgroundSize: 'auto 100%',
  imageRendering: isPixelated ? 'pixelated' : undefined,

  [mobileMediaQuery]: {
    backgroundSize: '100% auto'
  }
});

export const chooseStateScreenStyle = css({
  position: 'fixed',
  top: 0,
  left: 0,
  zIndex: 100,
  width: '100%',
  height: '100%',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  backgroundColor: Colors.BLACK
});

export const detailedWrapperInfo = css({
  width: '50vw',
  height: '70vh',
  zIndex: 5,

  [mobileMediaQuery]: {
    width: '100%'
  }
});
