import { typeToString } from './type-helpers';
import { ImageType } from '../enums';
import { IImageFileName } from '../types';

export type TExtracIImageFileNameResult = {
  type: ImageType;
  projectId: string;
  index: number;
};

export function extracIImageFileName(imageFileNameWithExt: IImageFileName): TExtracIImageFileNameResult {
  const [imageFileName] = typeToString(imageFileNameWithExt).split('.');
  const [projectId, type, index] = imageFileName.split('_');

  return {
    type: type as ImageType,
    index: Number(index),
    projectId
  };
}
