import React from 'react';
import { mainMobilePhotoStyle, mobileAnimationStyle } from './intro-photo-swiping.style';
import { TIntroPhotoSwipingProps } from './props.type';

const TIMEOUT_TO_HIDE = 2000;

export function IntroPhotoSwipingMobile({ onSwipeEnd, photoSrc }: TIntroPhotoSwipingProps): JSX.Element {
  const [hidden, setHidden] = React.useState(false);

  React.useEffect(() => {
    setTimeout(() => setHidden(true), TIMEOUT_TO_HIDE);
  }, []);

  return (
    <div
      className={hidden ? mobileAnimationStyle(photoSrc) : mainMobilePhotoStyle(photoSrc)}
      onTransitionEnd={onSwipeEnd}
    />
  );
}
