import { Layout } from 'components';
import React from 'react';
import { desktopPhotoContainer, mainDesktopPhotoStyle, desktopAnimationStyle } from './intro-photo-swiping.style';
import { TIntroPhotoSwipingProps } from './props.type';

const TIMEOUT_TO_HIDE = 2000;

export function IntroPhotoSwipingDesktop({ onSwipeEnd, photoSrc }: TIntroPhotoSwipingProps): JSX.Element {
  const [hidden, setHidden] = React.useState(false);

  React.useEffect(() => {
    setTimeout(() => setHidden(true), TIMEOUT_TO_HIDE);
  }, []);

  return (
    <Layout>
      <div className={desktopPhotoContainer}>
        <div
          className={hidden ? desktopAnimationStyle(photoSrc, '1') : mainDesktopPhotoStyle(photoSrc)}
          onTransitionEnd={onSwipeEnd}
        />
        <div
          className={hidden ? desktopAnimationStyle(photoSrc, '2') : mainDesktopPhotoStyle(photoSrc)}
        />
        <div
          className={hidden ? desktopAnimationStyle(photoSrc, '3') : mainDesktopPhotoStyle(photoSrc)}
        />
      </div>
    </Layout>
  );
}
