import { useMobile } from 'common';
import React from 'react';
import { IntroPhotoSwipingDesktop } from './intro-photo-swiping-desktop';
import { IntroPhotoSwipingMobile } from './intro-photo-swiping-mobile';
import { TIntroPhotoSwipingProps } from './props.type';

export function IntroPhotoSwiping({ onSwipeEnd, photoSrc }: TIntroPhotoSwipingProps): JSX.Element {
  const isMobile = useMobile();

  if (isMobile) {
    return <IntroPhotoSwipingMobile onSwipeEnd={onSwipeEnd} photoSrc={photoSrc} />;
  }

  return <IntroPhotoSwipingDesktop onSwipeEnd={onSwipeEnd} photoSrc={photoSrc} />;
}
