import { css } from '@emotion/css';
import { TLinkProps } from './link.props-type';
import { Colors } from '../colors';

type TRequiredLinkProps = Required<TLinkProps>;

const textSizeMap: Record<TRequiredLinkProps['size'], string> = {
  'l': '30px',
  'm': '12px'
};

const lineHeightMap: Record<TRequiredLinkProps['size'], string> = {
  'l': '27px',
  'm': '14px'
};

const textColorMap: Record<TRequiredLinkProps['color'], string> = {
  'black': Colors.BLACK,
  'white': Colors.WHITE,
  'red': Colors.RED
};

const hoverColorMap = (backgroundColor: Colors | null): Record<TRequiredLinkProps['color'], string> => {
  switch (backgroundColor) {
    case Colors.RED:
      return {
        'black': Colors.WHITE,
        'white': Colors.BLACK,
        'red': Colors.WHITE
      };
    case Colors.BLACK:
    case Colors.WHITE:
    default:
      return {
        'black': Colors.RED,
        'white': Colors.RED,
        'red': Colors.WHITE
      };
  }
};


const textFontFamilyMap: Record<TRequiredLinkProps['size'], string> = {
  'l': 'HelveticaNeue-Bold',
  'm': 'HelveticaNeueCyr-Bold'
};

export const linkStyle = (
  size: TRequiredLinkProps['size'],
  color: TRequiredLinkProps['color'],
  backgroundColor: Colors | null
): string => css({
  textDecoration: 'none',
  color: textColorMap[color],
  fontFamily: textFontFamilyMap[size],
  fontSize: textSizeMap[size],
  lineHeight: lineHeightMap[size],
  cursor: 'pointer',

  [':hover']: {
    color: hoverColorMap(backgroundColor)[color]
  }
});
