import { IImagePath, introService, useRx } from 'common';
import { ProcessingWrapper } from 'components';
import React from 'react';
import { IntroPhotoSwiping } from './intro-photo-swiping';
import { TIntroPhotoSwipingProps } from './props.type';

export function IntroPhotoSwipingWrapper({ onSwipeEnd }: Omit<TIntroPhotoSwipingProps, 'photoSrc'>): JSX.Element {
  const { src, isLoaded } = useRx(introService.photoData$);

  React.useEffect(() => {
    introService.initProcessPhoto();
  }, []);
  return (
    <ProcessingWrapper data={isLoaded && src} error={void 0}>
      {(loadedPhotoSrc: IImagePath) => <IntroPhotoSwiping onSwipeEnd={onSwipeEnd} photoSrc={loadedPhotoSrc}/>}
    </ProcessingWrapper>
  );
}
