import React from 'react';
import { imageStorage } from '../services';
import { IBase64ImagePlaceholder, IImagePath } from '../types';
import { fetchImagePromise } from '../utils';

type TUsePlaceholderImgResult = {
  pixelated: boolean;
  currentSrc: IImagePath | IBase64ImagePlaceholder;
};

export function usePlaceholderImg(imgSrc: IImagePath, isInView: boolean = true): TUsePlaceholderImgResult {
  const [pixelated, setPixelated] = React.useState(true);
  const [currentSrc, setCurrentSrc]
    = React.useState<IImagePath | IBase64ImagePlaceholder>(imageStorage.getImageBase64Data(imgSrc));
  const [isMounted, setIsMounted] = React.useState(true);

  React.useEffect(() => () => setIsMounted(false), []);

  React.useEffect(() => {
    if (isMounted && isInView) {
      setPixelated(true);
      setCurrentSrc(imageStorage.getImageBase64Data(imgSrc));

      fetchImagePromise(imgSrc).finally(() => {
        setPixelated(false);
        setCurrentSrc(imgSrc);
      });
    }
  }, [imgSrc, isMounted, isInView]);

  return { pixelated, currentSrc };
}
