import { IBase64ImagePlaceholder, IImagePath } from '../types';

export class ImageStorage {
  private storage = new Map<IImagePath, IBase64ImagePlaceholder>();

  setImageBase64Data(imageUrl: IImagePath, base64data: IBase64ImagePlaceholder): void {
    this.storage.set(imageUrl, base64data);
  }

  getImageBase64Data(imageUrl: IImagePath): IBase64ImagePlaceholder | IImagePath {
    return this.storage.get(imageUrl) || imageUrl;
  }
}
