import type { Colors } from 'components';

const defaultBgColor = 'rgba(0, 0, 0, 0)';

export function getInheritedBgColor(el: HTMLElement | null): Colors | null {
  if (!el) return null;

  // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
  const bg = window.getComputedStyle(el, null)['background-color'];

  if (bg === defaultBgColor) return getInheritedBgColor(el.parentElement);

  return bg as Colors;
}
