import { extracIImageFileName } from './extract-image-file-name';
import { stringToType, typeToString } from './type-helpers';

import { IImageFileName, IImagePath } from '../types';


function isResultImageFileName(result: unknown): result is IImageFileName {
  const tryExtractResult = extracIImageFileName(result as IImageFileName);

  return Boolean(tryExtractResult.projectId && tryExtractResult.type);
}

export function getImageNameFromPath(imagePath: IImagePath): IImageFileName {
  const result = imagePath.split('/').reverse()[0];

  const isImageName = isResultImageFileName(result);

  if (isImageName) {
    return stringToType<IImageFileName>(result);
  }

  throw new Error(`
    Название файла ${typeToString(imagePath)} не соответствует формату PROJECTID_IMGTYPE_INDEX.EXT
  `);
}
