import { css, cx } from '@emotion/css';
import { IImagePath, typeToString } from 'common';
import { fadeInKeyFrames } from 'components';

const indexDelayMap: Record<string, number> = {
  1: 1000,
  2: 500,
  3: 0
};

export const desktopPhotoContainer = css({
  display: 'flex',
  width: '100%',
  height: window.innerHeight,
  overflow: 'hidden'
});

const mainPhotoStyle = (background: IImagePath): string => css({
  height: window.innerHeight,
  backgroundImage: `url('${typeToString(background)}')`,
  backgroundPosition: 'center center',
  backgroundRepeat: 'repeat',
  backgroundSize: 'auto 100%',
  opacity: 0,
  animation: `${fadeInKeyFrames} 200ms forwards`
});

export const mainDesktopPhotoStyle = (background: IImagePath): string =>
  cx(mainPhotoStyle(background), css({
    width: '33.3%'
  }));

export const mainMobilePhotoStyle = (background: IImagePath): string =>
  cx(mainPhotoStyle(background), css({
    width: '100%'
  }));

export const mobileAnimationStyle = (background: IImagePath): string =>
  cx(mainMobilePhotoStyle(background), css({
    transition: 'transform ease-in 300ms',
    transform: 'translate3d(-100%, 0, 0)'
  }));

export const desktopAnimationStyle = (background: IImagePath, imgIndex: string): string =>
  cx(mainDesktopPhotoStyle(background), css({
    transition: `transform ease-in 500ms ${indexDelayMap[imgIndex]}ms`,
    transform: 'translate3d(-100vw, 0, 0)',
    zIndex: -Number(imgIndex)
  }));
