// eslint-disable-next-line @typescript-eslint/ban-types
export function typeToString<T extends String>(stringWithStrongType: T): string {
  return stringWithStrongType.toString();
}

// eslint-disable-next-line @typescript-eslint/ban-types
export function stringToType<T extends String>(string: string): T {
  return string as unknown as T;
}
