import type { Colors } from 'components';
import React from 'react';
import { getInheritedBgColor } from '../utils';

type TUseBgColorResult<T> = {
  color: Colors | null;
  ref: React.MutableRefObject<T | null>;
};

export function useBgColor<T extends HTMLElement>(): TUseBgColorResult<T> {
  const [color, setColor] = React.useState<Colors | null>(null);
  const ref = React.useRef<T | null>(null);

  React.useLayoutEffect(() => {
    window.setTimeout(() => {
      const bgColor = getInheritedBgColor(ref.current);
      setColor(bgColor);
    });
  }, []);

  return { color, ref };
}
