import { BehaviorSubject } from 'rxjs';
import { StaticProjectsStorage } from './static-projects.storage';
import { StorageService } from './storage.service';
import { IImagePath } from '../types';
import { fetchImagePromise, randomizeArray, stringToType } from '../utils';

type TIntroPhotoData = {
  src: IImagePath;
  isLoaded: boolean;
};

export class IntroService {
  private static initialPhotoData: TIntroPhotoData
  = { src: stringToType<IImagePath>(''), isLoaded: false };

  private storage = StorageService.makeInstance('intro', false);
  readonly introViewed$ = new BehaviorSubject(false);
  readonly photoData$ = new BehaviorSubject(IntroService.initialPhotoData);

  constructor(private staticProjectsStorage: StaticProjectsStorage) {
    this.updateFromStorage();
  }

  initProcessPhoto(): void {
    const photos = this.staticProjectsStorage.getIntroPhotos();
    const randomPhoto = photos.sort(randomizeArray)[0];

    void fetchImagePromise(randomPhoto).finally(() => this.photoData$.next({ src: randomPhoto, isLoaded: true }));
  }

  setState(value: boolean): void {
    this.storage.setState(value);
    this.updateFromStorage();
  }

  getState(): boolean {
    return this.storage.getState();
  }

  clear(): void {
    this.storage.setState(false);
  }

  private updateFromStorage(): void {
    const state = this.getState();

    this.introViewed$.next(state);
  }
}

