import { typeToString } from './type-helpers';
import { IImagePath } from '../types';

export function fetchImagePromise(imageUrl: IImagePath): Promise<boolean> {
  return new Promise((resolve: (val: boolean) => void, reject: () => void) => {
    const image = new Image();
    image.onload = () => resolve(true);
    image.onerror = reject;
    image.src = typeToString(imageUrl);
  });
}

export function fetchImages(images: Array<IImagePath>): void {
  images.forEach((imageUrl: IImagePath) => void fetchImagePromise(imageUrl));
}
