import { IImagePath, usePlaceholderImg } from 'common';
import React from 'react';
import { choosePageImageStyle } from './choose-page.style';

type TChoosePageImageProps = {
  imageUrl: IImagePath;
};

export function ChoosePageImage({ imageUrl }: TChoosePageImageProps): JSX.Element {
  const { currentSrc, pixelated } = usePlaceholderImg(imageUrl);

  return (
    <div className={choosePageImageStyle(currentSrc, pixelated)} />
  );
}
