import { stringToType, typeToString } from './type-helpers';
import { IImageFileName, IImagePath } from '../types';

export function geIImagePath(imgFileName: IImageFileName): IImagePath {
  // eslint-disable-next-line max-len
  return stringToType<IImagePath>(`${process.env.PUBLIC_URL}/${process.env.REACT_APP_ASSETS_PATH}${typeToString(imgFileName)}`);
}
