import React from 'react';
import { IntroPageSecond } from './intro-page-second';
import { IntroPhotoSwiping } from './intro-photo-swiping';
import { TIntroPageProps } from './prop.type';

enum Steps {
  FIRST,
  SECOND
}

export function IntroPage({ onSetIntroViewed }: TIntroPageProps): JSX.Element {
  const [step, setStep] = React.useState<Steps>(Steps.FIRST);

  const handleChangeStep = React.useCallback(() => setStep(Steps.SECOND), []);

  if (step === Steps.FIRST) return <IntroPhotoSwiping onSwipeEnd={handleChangeStep} />;
  if (step === Steps.SECOND) return <IntroPageSecond onSetIntroViewed={onSetIntroViewed} />;

  return <></>;
}
